<template>
    <view class="things_identity_select">
        <cu-custom bgColor="bg-gradual-blue" :isBack="true"><block slot="backText"></block><block slot="content">选择身份</block></cu-custom>
        <view class="text-center select_title">请选择您的身份</view>
        <view class="flex justify-between align-center identity_list">
            <view class="flex justify-center align-center identity_item" v-for="(item,index) in identityData" :key="index" :data-id="item.id" @tap="selectMemberFun">
                <image class="identity_icon" :src="'./../../../../static/images/things/identity/list/identity'+item.id+'.png'" mode="widthFix" />
                <view class="flex justify-center align-center identity_name">
                    <image v-if="item.id == select_index" class="select_icon" src="./../../../../static/images/global/select_true.png" mode="widthFix" />
                    <view class="name_text">{{item.name}}</view>
                </view>
                <view class="right_text">这里是会员权利</view>
            </view>
        </view>
        <button class="cu-btn next_btn" :disabled="buttonType" v-if="gradeInfo.is_upgrade_grade === 1" @tap="nextFun">下一步</button>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { getGradeInfo_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
    import { grabCarorder_format,grabCarOrderTip_format } from '@/utils/filters.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            gradeInfo: {},//用户等级信息
            buttonType: false,
            preRrefresh: false,// 判断上一页是否要刷新
            select_index: 1,
            identityData: [
                {
                    id: 1,
                    name: '普通会员',
                    icon: ''
                },
                {
                    id: 2,
                    name: '企业会员',
                    icon: ''
                },
                {
                    id: 3,
                    name: '代言人',
                    icon: ''
                },
            ],

        }
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**跳转到填写会员页面
         * @description: 
         * @param {*} e
         * @return {*}
         */
        nextFun(e) {
            if (that.select_index === that.gradeInfo.type) {
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'您已经是当前会员了'}});
                return false;
            }
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/identity/form/index?pageType=${that.select_index}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            })
        },
        /**选择要升级的会员
         * @description: 
         * @param {*} e
         * @return {*}
         */
        selectMemberFun(e) {
            if (e.currentTarget.dataset.id === that.select_index) return false;
            that.select_index = e.currentTarget.dataset.id;
        },
        /**获取用户等级信息
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getGradeInfo(e) {
            getGradeInfo_api({},that).then(res=>{
                that.gradeInfo = res;
            }).catch(err=>{
                console.log(err);
            })
        }
    },
    onLoad: function(options) {
        that = this;
        that.getGradeInfo();
    }
}
</script>
<style lang="scss" scoped>
    @import './index';
</style>